import * as React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import { Badge, Col, Row } from 'react-bootstrap';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import { TbDownload, TbUpload } from 'react-icons/tb';
import { IoCopyOutline } from 'react-icons/io5';


const App = () => {

  const [commands, setCommands] = React.useState([]);
  const [command, setCommand] = React.useState({});

  const debounce = (callback, wait) => {
    let timeout;
    return (...args) => {
      const context = this;
      clearTimeout(timeout);
      timeout = setTimeout(() => callback.apply(context, args), wait);
    };
  }

  const copy = (that) => {
    const inp = document.createElement('input');
    document.body.appendChild(inp)
    inp.value = that
    inp.select();
    document.execCommand('copy', false);
    inp.remove();
  }

  const handleList = (items) => {
    if (items) {
      return items.split(',');
    }
  }

  const addBlock = () => {
    if (command.title && command.list?.length > 0) {
      setCommands(
        [
          ...commands,
          {
            title: command.title,
            list: command.list
          }
        ]
      )

      setCommand({});
    }
  }

  const deleteCommands = () => {
    localStorage.removeItem('commands')
    setCommands([]);
  }

  React.useEffect(() => {
    if (localStorage.getItem('commands')) {
      setCommands(JSON.parse(localStorage.getItem('commands')));
    }
  }, []);

  React.useEffect(() => {
    if (commands.length > 0) {
      localStorage.setItem('commands', JSON.stringify(commands));
    }
  }, [commands]);

  return (
    <React.Fragment>
      <Row className='g-0'>
        <Col>
          <Modal.Dialog className='m-4'>
            <Modal.Header>
              <Modal.Title>Add a new block</Modal.Title>
            </Modal.Header>

            <Modal.Body>
              <p>Add a new block of commands to the right side.</p>

              <Form.Label htmlFor="inputPassword5">Block title</Form.Label>
              <Form.Control
                // value={command.title}
                onChange={debounce((e) => { setCommand({ ...command, title: e.target.value }) }, 1000)}
              />
              <Form.Text id="passwordHelpBlock" muted>
                This is the title that will be on the accordion's header.
              </Form.Text>

              <Form.Group className="my-3" controlId="exampleForm.ControlTextarea1">
                <Form.Label>Insert commands, separated by comma.</Form.Label>
                <Form.Control as="textarea" placeholder='command 1, command 2, comm...' rows={4}
                  // value={command.list?.join(',')}
                  onChange={debounce((e) => { setCommand({ ...command, list: handleList(e.target.value) }) }, 500)}
                />
              </Form.Group>

              <Button onClick={() => { addBlock() }} disabled={!command.title || !command.list} className='w-100' variant="primary" type="submit">
                Add block
              </Button>
              <p className='mt-2 small'>* Those commands will be saved on your localStorage.</p>
            </Modal.Body>
          </Modal.Dialog>

          <hr />

          <Modal.Dialog className='m-4'>
            <Modal.Header>
              <Modal.Title>Export / Import</Modal.Title>
            </Modal.Header>

            <Modal.Body className='my-2'>
              <p className='mb-1'>You can export a file containing the commands to import in another browser.</p>
              <p>By importing, you'll replace all you have saved now with the new commands from imported file.</p>
              <Row className='my-4'>
                <Col>
                  <Button className='w-100' variant="light" type="submit">
                    Export (.json)
                    <TbDownload className='mx-2' />
                  </Button>
                </Col>
                <Col>
                  <Button className='w-100' variant="light" type="submit">
                    Import (.json)
                    <TbUpload className='mx-2' />
                  </Button>
                </Col>
              </Row>
            </Modal.Body>
          </Modal.Dialog>

        </Col>
        <Col style={{ backgroundColor: '#f9f9f9', height: '100vh', padding: '15px', maxHeight: '100vh', overflow: 'auto' }}>
          <Accordion defaultActiveKey="0" className='h-100'>
            {
              !!commands.length ? (
                <React.Fragment>
                  {
                    commands?.map((el, index) => {
                      return (
                        <Accordion.Item eventKey={index} key={index}>
                          <Accordion.Header>{el.title}</Accordion.Header>
                          <Accordion.Body className='limited-height'>
                            <ul>
                              {
                                el.list.map((el, index) => {
                                  return (
                                    <li className="command-li my-2 d-grid gap-2" key={index}>
                                      <Button onClick={() => { copy(el) }} className="d-flex justify-content-between align-items-center" variant="light">
                                        <code>{el}</code>
                                        <Badge pill bg="primary">
                                          <IoCopyOutline />
                                          <span className='mx-1 float-right'>click anywhere to copy</span>
                                        </Badge>
                                      </Button>
                                    </li>
                                  )
                                })
                              }
                            </ul>
                          </Accordion.Body>
                        </Accordion.Item>
                      )
                    })
                  }
                  <Button onClick={() => { deleteCommands() }} className='w-100 my-3' variant="outline-danger">
                    Delete all blocks of commands
                  </Button>
                </React.Fragment>
              ) : (
                <div className="centered-item h-100">
                  <p className='text-danger mb-0'><strong>There's no blocks of commands to show.</strong></p>
                  <p>Add a new one on the left side.</p>
                </div>
              )
            }
          </Accordion>
        </Col>
      </Row>
    </React.Fragment >
  );
}

export default App;
